import React from 'react';
// eslint-disable-next-line 
import { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getRecipes } from '../actions';
import { Link } from 'react-router-dom';
import Card from './Card';

export default function Home (){

    const dispatch = useDispatch ()
    const allRecipes = useSelector ((state) => state.recipes)
     //con useSelector traeme todo lo que esta en el estado recipes
//traernos del estado a los recipes cuando se monta usando useEfect es para montar

useEffect(() =>{ // cada vez que se monta se me llena el estado, y luego se salgo se descarga desmonta
    dispatch(getRecipes());
},[dispatch]) //para que no me haga un loop infinito [dispacth] siempre y cuando hace el dispatch

//creacion de funciones para botones
function handleClick(e) { //esta funcion es para resetear las recetas
  e.preventDefault();  //preventivo para que no se rompa la pagina
  dispatch(getRecipes());
}
// ['gluten free', 'ketogenic', 'vegetarian', 
// 'lacto vegetarian','ovo vegetarian', 'lacto ovo vegetarian', 'vegan',
//  'pescetarian', 'paleolithic', 'primal', 'low fodmap', 'whole 30', 'dairy free'];


//renderizamos
return (
    <div>
        <Link to = '/recipes'>Crear Recetas</Link>
        <h1>Recetas</h1>
        <button onClick={e=> {handleClick(e)}}>Volver a cargar Recetas</button>
        <div> 
            <select>
                <option value='gluten free'>Libre de Gluten</option>
                <option value='ketogenic'>Cetogénico</option>
                <option value='vegetarian'>Vegetariano</option>
                <option value='lacto vegetatian'>Lactovegetariano</option>
                <option value='ovo vegetarian'>Ovo-vegetariano</option>
                <option value='lacto ovo vegetarian'>Lacto-Ovo-Vegetariano</option>
                <option value='vegan'>Vegano</option>
                <option value='pescetarian'>Pescetariano</option>
                <option value='paleolithic'>Paleo</option>
                <option value='primal'>Primitivo</option>
                <option value='low fodmap'>FODMAP bajo</option>
                <option value='whole 30'>Entero 30</option>
                <option value='dairy free'>Libre de lácteos</option>
            </select>
            <select>
                <option value='asc'>Ascendente</option>
                <option value='desc'>Descendente</option>
            </select>
            <select>
                <option disabled selected>Paginado</option>
                <option value="atoz">A to Z</option>
                <option value="ztoa">Z to A</option>
             </select>
            {//hay recipes => mapeo de mi Card.jsx
                allRecipes?.map((c) => { //existe? si existe mapea
                    return(
                        <div className='cards'>
                            <Link to={"/home/" + c.id}>
                               <Card name={c.name} image={c.image} dietBd={c.dietBd} key={c.id}/>
                            </Link>
                        </div>
                    )
                })}
            
        </div>
    </div>
 ) }