import React from 'react';

//renderiza lo que necesito
export default function Recipe(recipes) {
    const { name,  image, dietBd } = recipes
     return(
        <div>
            <h1>Holaaaaa probando desde cards</h1>
            <h3>{name}</h3>
            <img src={image} alt="img not fount" width='200px' height='250px'/>
            <h5>{dietBd}</h5>
        </div>
    )
}

