import axios from 'axios';


//redux thank me sirve aca para hacer las llamadas asincronas

//get para traer todas las recetas
//ACA SURGE LA CONEXION BACK CON FRONT

export function getRecipes(){
    return async function (dispatch){
        const json = await axios.get("http://localhost:3001/recipes");
        return dispatch({ //dispacht la accion
            type: 'GET_RECIPES',
            payload: json.data
        })
    }
}

