/* eslint-disable default-case */
const initialState = { 
    recipes : []
}
function rootReducer(state = initialState, action){
    switch(action.type) {
        case 'GET_RECIPES':
            return{
                ...state,
                recipes: action.payload, //en mi estado recipes [vacio] manda todo lo que tengas en la action recipes
            }
        default:
            return state;
    }

}
export default rootReducer 